export function getDromInfo(data = {}) {
  const paramsObj = {
    method: 'getDromInfo',
    request_data: Object.assign(data)
  }
  return request({
    url: APIURL,
    method: 'post',
    data: paramsObj,
    headers: {
      loadingText: encodeURI('')
    }
  })
}
